import React from 'react'
import { Route, Routes } from 'react-router-dom'
import Home from './Home'
import Featured from './Featured'
import Pricing from './Pricing'
import Center from '../center/Center'
import ProductDetailById from '../center/ProductDetailById'
import Productdetail from '../QueryParam/Productdetail'
import Permanentjob from './Permanentjob'
import Contractualjob from './Contractualjob'

export default function RouteContainer() {
  return (
    <Routes>
    <Route exact path="/" element={<Home />} />
    <Route exact path="/home" element={<Home />} />
    <Route exact path="/featured" element={<Featured />}>
      <Route index element={<Permanentjob />}/>
      <Route path="permanent" element={<Permanentjob />}/>
      <Route path="contractual" element={<Contractualjob />}/>
    </Route>
    <Route exact path="/center" element={<Center />} />
    <Route exact path="/productdetails/:id" element={<ProductDetailById />} />
    <Route  path="/productdetail" element={<Productdetail />} />
    <Route path="*" element={<Pricing />} /> (No Match Route)
</Routes>	
  )
}
