import React from 'react';
import { Link } from 'react-router-dom';
// import './categories.css'

export default function Product(props) {
    let propObj = props.product;
  return (
    <>
    <div class="card text-center mt-3 h-100">
                                <img src={propObj.image}  class="card-img-top" alt="..." style={{height: 15+'rem', width:16+'rem'}} />
                                <div class="card-body">
                                    <h5 class="card-title">{propObj.title}</h5>
                                    <p class="card-text">Category:{propObj.category}</p>
                                    <p class="card-text">Price:{propObj.price}</p>
                                    <p class="card-text">Rate:{propObj.rating.rate}</p>
                                    {/* <a href="#" class="btn btn-primary">Add to cart</a> */}
                                    <button>
                                      <Link to={`/productdetails/${propObj.id}`}>View Details</Link>
                                    </button>
                                    

                                </div>
                            </div>

    </>
  )
} 