import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'

export default function ProductDetailById() {
    const {id} = useParams();
    const [product,setProduct] = useState({});
    const fetchProduct = async function(){
        const resp = await axios.get(`https://fakestoreapi.com/products/${id}`);
        setProduct(resp.data);
    }
    useEffect(()=>{
        fetchProduct();

    },[id]);
  return (
    <>
    {/* <h1>Product details for id-{id}</h1> */}
    <div class="card text-center mt-3 h-100">
                                <img src={product.image}  class="card-img-top" alt="..." style={{height: 15+'rem', width:16+'rem'}} />
                                <div class="card-body">
                                    <h5 class="card-title">{product.title}</h5>
                                    <p class="card-text">Category:{product.category}</p>
                                    <p class="card-text">Price:{product.price}</p>
                                    {/* <p class="card-text">Rate:{product.rating.rate}</p> */}
                                    {/* <a href="#" class="btn btn-primary">Add to cart</a> */}
                                </div>
                            </div>

    </>
  )
}
