import React, { useState } from "react";
import products from "../center/Products.json";
import { createSearchParams, useNavigate } from "react-router-dom";
import Productdetail from './Productdetail'

export default function ProductList() {
  // const [product, setProduct] = useState(products);
  const navigate = useNavigate();
  const navigateHandler = (title, price) => {
    navigate({
      pathname: "/productdetail",
      search: `?${createSearchParams({ title, price })}`,
    });
  };
  console.log(products);
  return (
    <>
      <div className="container">
        <div className="row">
        {products.map((product) => {
          return (
            <div className="col-sm-3">
              <div class="card text-center mt-3 h-100 g-4">
                <img
                  src={product.image}
                  class="card-img-top"
                  alt="..."
                  style={{ height: 15 + "rem", width: 16 + "rem" }}
                />
                <div class="card-body">
                  <h5 class="card-title">{product.title}</h5>
                  <p class="card-text">Category:{product.category}</p>
                  <p class="card-text">Price:{product.price}</p>
                  <p class="card-text">Rate:{product.rating.rate}</p>
                  {/* <a href="#" class="btn btn-primary">Add to cart</a> */}
                  <button
                    className="btn btn-secondary"
                    onClick={() => {
                      navigateHandler(product.title, product.price);
                    }}
                  >
                    query param
                  </button>
                </div>
              </div>
            </div>
          );
        })}
        </div>
      </div>
    </>
  );
}
